
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 16px;
  font: inherit;
}
input,
textarea {
  outline: none;
}
textarea {
  resize: none;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.4;
  color: #2c3e50;
}

.battery-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.battery {
  width: 200px;
  height: 400px;
  margin: 50px 10px;
  background-color: slategray;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
@media screen and (max-width: 400px) {
.battery {
    width: 100%;
}
}
.battery__body {
  -webkit-box-flex: 20;
      -ms-flex-positive: 20;
          flex-grow: 20;
  position: relative;
}
.battery__charge {
  position: absolute;
  left: 0;
  bottom: 0;
  background: coral;
  width: 100%;
  height: 0;
  -webkit-transition: height .2s ease;
  transition: height .2s ease;
}
.battery__text {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-align: center;
  font-weight: bold;
  font-size: 40px;
  color: #2c3e50;
}
.battery__tools {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.battery__btn {
  display: inline-block;
  width: 44px;
  height: 44px;
  position: relative;
  background: white;
}
.battery__btn:before {
  content: '';
  width: 14px;
  height: 2px;
  background: black;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -1px;
  margin-left: -7px;
}
.battery__btn:after {
  content: '';
  width: 2px;
  height: 14px;
  background: black;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -7px;
  margin-left: -1px;
}
.battery__btn_minus {
  border-right: 1px solid black;
}
.battery__btn_minus:after {
  display: none;
}
.battery__btn_plus {
  border-left: 1px solid black;
}
.battery__btn-text {
  text-align: center;
  font-size: 17px;
  height: 34px;
  line-height: 32px;
  padding: 0 20px;
  color: black;
  background: white;
}
